import request from '@/utils/request'
//查询客户列表
export function getUserManagementList(data) {
  return request({
    url: `/user/business/userMember/getUserManagementList`,
    method: 'post',
    data: data
  })
}
//客户详情
export function getUserDetails(data) {
  return request({
    url: `/user/business/userMember/getUserDetails`,
    method: 'post',
    data: data
  })
}
//修改备注和客户信息
export function updateUserDetails(data) {
  return request({
    url: `/user/business/userMember/updateUserDetails`,
    method: 'post',
    data: data
  })
}
// 送会员
export function saveUserVipRecord(data) {
  return request({
    url: `/user/business/userMember/saveUserVipRecord`,
    method: 'post',
    data: data
  })
}
// 停用
export function prohibitUserVip(data) {
  return request({
    url: `/user/business/userMember/prohibitUserVip?id=${data}`,
    method: 'post',
  })
}