<template>
  <div class="pageContent"> 
    <el-form label-width="140px" :model="form" class="demo-ruleForm">
      <!-- 基本信息 -->
      <el-card class="box-card" style="margin-bottom:20px;">
        <div class="pageHead">基本信息</div>
        <div class="flex-row-start">
          <el-avatar style="margin-right:40px;" shape="square" :size="100" fit="fill" :src="form.headImageUrl ? form.headImageUrl : 'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png'"></el-avatar>
          <div style="flex:1">
            <div class="flex-row-start">
              <div class="flex-row-start" style="margin-right:30px;">
                <div class="item itemLabel">最近浏览时间:</div>
                <div class="item itemValue">{{form.lastAccess ? form.lastAccess : '-'}}</div>
              </div>
              <div class="flex-row-start" style="margin-right:30px;">
                <div class="item itemLabel">手机号:</div>
                <div class="item itemValue">{{form.cellPhone ? form.cellPhone : '-'}}</div>
              </div>
              <div class="flex-row-start" style="margin-right:30px;">
                <div class="item itemLabel">微信昵称:</div>
                <div class="item itemValue">{{form.nickName ? form.nickName : '-'}}</div>
              </div>
            </div>
            <div class="flex-row-start">
              <div class="flex-row-start" style="margin-right:30px;">
                <div class="item itemLabel">成为客户时间:</div>
                <div class="item itemValue">
                  {{form.firstTime}}
                  <el-tooltip class="item" effect="dark" content="成为客户时间为用户首次下单时间" placement="top">
                    <i class="el-icon-question"></i>
                  </el-tooltip>
                </div>
              </div>
              <div class="flex-row-start" style="margin-right:30px;">
                <div class="item itemLabel">收货地址:</div>
                <div class="item itemValue">{{form.address ? form.address : '-'}}</div>
              </div>
            </div>
            <div class="flex-row-start" v-if="form.isVip == 2">
              <div class="flex-row-start" style="margin-right:30px;">
                <div class="item itemLabel">会员到期时间:</div>
                <div class="item itemValue">
                  {{form.endTime}}
                </div>
              </div>
            </div>
            <div class="flex-row-start">
              <div class="flex-row-start" style="margin-right:30px;">
                <div class="item itemLabel">备注:</div>
                <div class="item itemValue">
                  {{form.remark}}
                  <el-button style="margin-left:30px" type="text" @click="remarkEdit">编辑</el-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </el-card>
      <!-- 客户资料 -->
      <el-card class="box-card" style="margin-bottom:20px;">
        <div class="pageHead">
          客户资料
          <el-button style="margin-left:50px;" @click="showEdit">编辑</el-button>
        </div>
        <div class="flex-row-start flex-wrap">
          <div class="flex-row-start" style="margin-right:30px;">
            <div class="item itemLabel">姓名:</div>
            <div class="item itemValue">{{form.userName ? form.userName : '-'}}</div>
          </div>
          <div class="flex-row-start" style="margin-right:30px;">
            <div class="item itemLabel">性别:</div>
            <div class="item itemValue" v-if="form.sex == 1">男</div>
            <div class="item itemValue" v-if="form.sex == 2">女</div>
            <div class="item itemValue" v-if="form.sex == 0">未知</div>
          </div>
          <div class="flex-row-start" style="margin-right:30px;">
            <div class="item itemLabel">生日:</div>
            <div class="item itemValue">{{form.birthday ? form.birthday : '-'}}</div>
          </div>
          <div class="flex-row-start" style="margin-right:30px;">
            <div class="item itemLabel">所在地区:</div>
            <div class="item itemValue">{{form.allAddress}}</div>
          </div>
          <div class="flex-row-start" style="margin-right:30px;">
            <div class="item itemLabel">微信号:</div>
            <div class="item itemValue">{{form.wechatNumber ? form.wechatNumber : '-'}}</div>
          </div>
        </div>
      </el-card>
      <!-- 交易统计 -->
      <el-card class="box-card" style="margin-bottom:20px;">
        <div class="pageHead">
          交易统计
          <el-button style="margin-left:50px;" @click="newPage">订单查询</el-button>
        </div>
        <el-table :data="[form]">
          <el-table-column prop="lastTime" label="最近下单时间"></el-table-column>
          <el-table-column prop="userPrice" label="客单价"></el-table-column>
          <el-table-column prop="orderAmount" label="累计消费金额"></el-table-column>
          <el-table-column prop="orderTotal" label="累计消费订单数"></el-table-column>
          <el-table-column prop="refundAmount" label="累计退款金额"></el-table-column>
          <el-table-column prop="refundTotal" label="累计退款商品数"></el-table-column>
        </el-table>
      </el-card>
      <!-- 资产信息 -->
      <el-card class="box-card" style="margin-bottom:20px;">
        <div class="pageHead">资产信息</div>
      </el-card>
      <!-- 会员权益 -->
      <el-card class="box-card" style="margin-bottom:20px;">
        <div class="pageHead">会员权益</div>
      </el-card>
    </el-form>
    <!-- 编辑备注 -->
    <el-dialog title="编辑备注" :visible.sync="remarkEditShow" center width="50%" :close-on-click-modal="false" :close-on-press-escape="false">
      <el-input type="textarea" placeholder="请输入内容" v-model="remarkEditForm.remark" maxlength="50" show-word-limit @input="change($event)"></el-input>
        <div slot="footer" class="dialog-footer">
          <el-button @click="remarkEditShow = false">取 消</el-button>
          <el-button :loading="remarkEditLoading" type="primary" @click="editSub">确 定</el-button>
        </div>
      </el-dialog>
    <!-- 编辑客户资料 -->
    <el-dialog title="编辑客户资料" :visible.sync="editCustomerShow" width="70%" :close-on-click-modal="false" :close-on-press-escape="false">
      <el-form :model="customerEditForm"  label-width="140px" class="demo-ruleForm">
        <el-form-item label="客户姓名：" required>
          <el-input style="width:400px;" type="text" placeholder="请输入姓名" v-model="customerEditForm.userName" maxlength="10" show-word-limit @input="change">
          </el-input>
        </el-form-item>
        <el-form-item label="性别：" required>
          <el-select v-model="customerEditForm.sex" clearable placeholder="请选择">
            <el-option label="未知" value="0"></el-option>
            <el-option label="男" value="1"></el-option>
            <el-option label="女" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="生日：" required>
          <el-date-picker
            v-model="customerEditForm.birthday"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="所在地区：" required>
          <el-input style="width:400px;" type="text" placeholder="请输入地址" v-model="customerEditForm.address" maxlength="30" show-word-limit @input="change">
          </el-input>
        </el-form-item>
        <el-form-item label="微信号：" required>
          <el-input style="max-width:400px;" v-model.trim="customerEditForm.wechatNumber" maxlength="20" show-word-limit @input="change"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="editCustomerShow = false">取消</el-button>
          <el-button :loading="editCustomerLoading" type="primary" @click="subEditChange">确定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getUserDetails,updateUserDetails } from '@/api/customer/customerManageList'
export default {
  data(){
    return{
      form:{},
      remarkEditShow:false,               // 编辑备注弹出框
      remarkEditLoading:false,            // 编辑备注loading
      remarkEditForm:{},                  // 编辑备注表格

      customerEditForm:{},                // 客户编辑
      editCustomerShow:false,             // 编辑客户 弹出框
      editCustomerLoading:false,          // 编辑客户loading
    }
  },
  created(){
    // 编辑备注
    this.remarkEditForm.id = this.$route.query.id
    this.getData()
  },
  methods:{
    // 获取详情
    getData(){
      getUserDetails({id:this.$route.query.id}).then(res=>{
        console.log('获取详情',res)
        if(res.code == 200){
          this.form = res.data;
          this.form.allAddress = res.data.provinceName + res.data.cityName + res.data.districtName + res.data.address
        }
      })
    },
    // 跳转订单
    newPage(){
      let routeUrl = this.$router.resolve({
        path: '/order/orderList',
        query:{
          receiverPhone:this.form.cellPhone
        }
      });
      window.open(routeUrl.href, '_blank');
    },
    //打开编辑客户资料弹框
    showEdit(){
      console.log("编辑客户资料");
      let obj = {
        userName:this.form.userName,
        sex:''+this.form.sex,
        birthday:this.form.birthday,
        address:this.form.allAddress,
        wechatNumber:this.form.wechatNumber
      }
      this.customerEditForm = obj
      this.editCustomerShow = true;
    },
    //更新客户资料
    subEditChange(){
      this.customerEditForm.id = this.$route.query.id
      console.log("更新客户资料请求的数据",this.customerEditForm);
      this.editCustomerLoading = true;
      updateUserDetails(this.customerEditForm).then(res=>{
        console.log('更新客户资料',res);
        if(res.code == 200){
          console.log("成功",res.data);
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.getData()
          this.editCustomerShow = false;
          setTimeout(()=>{
            this.editCustomerLoading = false;
          },1000)
        }else{
          this.editCustomerLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 编辑备注弹出框
    remarkEdit(){
      this.remarkEditForm.remark = this.form.remark
      this.remarkEditShow = true;
    },
    //编辑备注
    editSub(){
      console.log("编辑备注");
      if(!this.remarkEditForm.remark){
        this.$message.error('请输入备注内容');
        return;
      }
      if(this.remarkEditForm.remark.length > 50){
        this.$message.error('备注内容过长，在50字之内');
        return;
      }
      this.remarkEditLoading = true;
      updateUserDetails(this.remarkEditForm).then(res=>{
        console.log('编辑备注',res);
        if(res.code == 200){
          console.log("成功",res.data);
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.getData()
          this.remarkEditShow = false;
          setTimeout(()=>{
            this.remarkEditLoading = false;
          },1000)
        }else{
          this.remarkEditLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .item{
    font-size: 14px;
    line-height: 40px;
    &.itemLabel{
      color: #606266;
      margin-right: 10px;
    }
    &.itemValue{
      color: #303133;
    }
  }
  
</style>